import {Fighter} from './Fighter';

/**
 * This is a class that extends another class
 */
export class DC extends Fighter {
    /**
     * Constructor de la clase DC, no implementa nada nuevo a la de Fighter
     * @param nombre es el nombre del peleador
     * @param peso su peso 
     * @param altura su altura
     * @param ataque su ataque
     * @param defensa la defensa que tiene sobre el ataque
     * @param hp la cantidad de ataque que resiste
     * @param velocidad la velocidad de ataque
     */
    constructor(protected readonly nombre: string, protected peso: number, protected altura: number, protected ataque: number, protected defensa: number, protected hp: number, protected velocidad: number) {
        super(nombre, peso, altura, ataque, defensa, hp, velocidad);
    }
    /**
     * Es el metodo abastracto que tienen que implementar las clases derivadas de Figther
     * @return retorna el grito de ataque del personaje de DC
     */
    gritoDeAtaque() {
        return "Yo " + this.nombre + " te lanzo un SUPER ataque!!";
    }
}